h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
body {
    font-family: "Roboto", sans-serif;
}
hr {
    margin-right: -30px;
    margin-left: -30px;
    margin-top: 0;
    margin-bottom: 0;
}
h2 {
    font-family: Micra;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 22px;
    color: #000000;
}
ul {
    list-style: none;
}
button {
    border: none;
}
a {
    color: inherit;
}
a:hover {
    text-decoration: none;
    color: inherit;
}
.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}
.section--header {
    margin-top: 13px;
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &--logo {
            display: flex;
            align-items: center;
            width: 31%;
            .logotype {
                margin-left: 15px;
                .logo {
                    font-style: normal;
                    font-weight: bold;
                    font-size: 32px;
                    line-height: 32px;
                    text-transform: uppercase;
                }
                .sublogo {
                    font-family: "Micra", arial;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 12px;
                    line-height: 14px;
                    color: #565656;
                }
            }
            img {
                width: 70px;
            }
        }
        &--nav {
            .top-section {
                display: flex;
                align-items: center;
                margin-top: 5px;
                .navigation {
                    ul {
                        display: flex;
                        align-items: center;
                        li {
                            font-family: "Micra", arial;
                            font-style: normal;
                            font-weight: normal;
                            font-size: 11px;
                            margin-right: 23px;
                            line-height: 1.1;
                            color: #565656;
                        }
                    }
                }
                .authorization {
                    box-sizing: border-box;
                    display: flex;
                    border: 1px solid #e2e2e2;
                    background: #ffffff;
                    border-radius: 2px;
                    margin-left: 4px;
                    .login {
                        font-style: normal;
                        font-weight: normal;
                        border-right: 1px solid #ececec;
                        font-size: 12px;
                        line-height: 18px;
                        padding: 5px 12px 5px 12px;
                        text-align: center;
                        color: #565656;
                    }
                    .signup {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 12px;
                        padding: 5px 12px 5px 12px;
                        line-height: 18px;
                        text-align: center;
                        color: #565656;
                    }
                }
            }
            .bottom-section {
                display: flex;
                margin-top: 12px;
                align-items: center;
                justify-content: space-between;
                .address {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 18px;
                    text-align: left;
                    color: #bbbbbb;
                    font-family: "Micra";
                    a {
                        color: #7d7d7d;
                    }
                }
                .cart {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    .wrapper {
                        .title {

                            font-family: Micra;
                            font-style: normal;
                            font-weight: normal;
                            font-size: 12px;
                            line-height: 1;
                            color: #000000;
                        }
                        .subtitle {
                            font-style: normal;
                            font-weight: normal;
                            font-size: 12px;
                            line-height: 1.8;
                            color: #c2c2c2;
                        }
                    }
                    img {
                        width: 28px;
                        margin-right: 5px;
                        padding-bottom: 10px;
                    }
                }
            }
        }
    }
}
.section__search {
    margin-top: 25px;
    &--form {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        position: relative;
        border: 1px solid #dfdfdf;
        padding: 18px 29px 19px 29px;
        background: #ffffff;
        border-radius: 0;
        span {
            position: absolute;
            right: 185px;
            cursor: pointer;
            img {
                width: 20px;
            }
        }
        input {
            box-sizing: border-box;
            width: 100%;
            border: 1px solid #dddddd;
            padding: 8px 9px 8px 9px;
            margin-right: 25px;
            background: #f1f1f1;
            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
            border-radius: 3px;
            &:focus {
                outline: none;
            }
            &::placeholder {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 12px;
                color: #878787;
            }
        }
        button {
            height: 36px;
            padding: 0 40px 0 40px;
            background: #b01e2f;
            border-radius: 3px;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 30px;
            text-align: center;
            color: #ffffff;
        }
    }
}
.section__content {
    margin-top: 25px;
    &--wrapper {
        padding: 0 30px 0 30px;
        background: white;
        border-bottom: 1px solid #ced7e0;
        .section__content--main--crumbs {
            margin-bottom: 30px;
            ul {
                display: flex;
                li {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 490%;
                    color: #333333;
                    margin-right: 19px;
                    &:after {
                        content: "";
                        display: inline-block;
                        width: 0;
                        height: 0;
                        border-style: solid;
                        border-width: 4px 0 4px 10px;
                        border-color: transparent transparent transparent #c4c4c4;
                        margin-left: 14px;
                    }
                    &:nth-last-child(1) {
                        &:after {
                            content: none;
                        }
                    }
                }
            }
        }
    }
    &--sidebar {
        box-sizing: border-box;
        border: 1px solid #dfdfdf;
        background: #ffffff;
        border-radius: 0;
        padding: 30px 0 30px 0;
        ul {
            li {
                display: flex;
                align-items: center;
                height: 40px;
                padding-left: 24px;
                cursor: pointer;
                &.active {
                    background: #efefef;
                }
            }
        }
    }
    &--main {
        border: 1px solid #e4e4e4;
        &--product {
            .recommend {
                .body {
                    .item {
                        margin-bottom: 34px;
                        .image {
                            margin-bottom: 12px;
                            img {
                                width: 100%;
                            }
                        }
                        .body {
                            .instock {
                                font-style: normal;
                                font-weight: normal;
                                font-size: 14px;
                                line-height: 18px;
                                color: #a4a4a4;
                            }
                        }
                    }
                }
                .header {
                    font-family: Micra;
                    font-style: normal;
                    font-weight: normal;
                    margin-bottom: 15px;
                    font-size: 22px;
                    line-height: 1;
                    color: #000000;
                }
            }
            .tabs {
                margin-bottom: 40px;
                .tab-content {
                    margin-bottom: 60px;
                    margin-top: 40px;
                    h2 {
                        margin-bottom: 15px;
                    }
                    h3 {
                        font-family: Roboto;
                        font-style: normal;
                        margin-bottom: 15px;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 1.1;
                        text-transform: uppercase;
                        color: #000000;
                    }
                }
                .nav-tabs {
                    border-bottom: none;
                    background: #f7f7f7;
                    border-radius: 0;
                    margin-left: -30px;
                    margin-right: -30px;
                }
                .nav-item {
                    font-family: Roboto;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 2.5;
                    color: #000000;
                    .nav-link {
                        padding: 0.7rem 1.2rem;
                        &.active {
                            color: #495057;
                            background-color: #fff;
                            border-color: #dddddd;
                            border-bottom: none;
                        }
                    }
                }
            }
            .header {
                .top-section {
                    display: flex;
                    justify-content: space-between;
                    .title {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 22px;
                        line-height: 22px;
                        color: #000000;
                    }
                }
                .bottom-section {
                    display: flex;
                    margin-bottom: 15px;
                    .raiting {
                        margin-right: 25px;
                        img {
                            width: 20px;
                        }
                    }
                }
            }
            .body {
                .body--wrapper {
                    .buy-button {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 42px;
                        background: #6c6c6c;
                        border-radius: 3px;
                        color: white;
                        cursor: pointer;
                        margin-bottom: 15px;
                    }
                    .icons {
                        .icon {
                            display: flex;
                            align-items: flex-start;
                            margin-bottom: 15px;
                            img {
                                width: 20px;
                            }
                            .title {
                                margin: 0 0 0 15px;
                                font-style: normal;
                                font-weight: normal;
                                font-size: 14px;
                                line-height: 1.7;
                                color: #707070;
                                span {
                                    color: #bdbdbd;
                                    cursor: pointer;
                                }
                                p {
                                    margin-bottom: 7px;
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                    .incart-button {
                        box-sizing: border-box;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 42px;
                        border: 1px solid #d9d9d9;
                        background: #ffffff;
                        cursor: pointer;
                        border-radius: 3px;
                        margin-bottom: 15px;
                    }
                    .title {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 1.4;
                        color: #707070;
                    }
                    .price {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 36px;
                        line-height: 1.6;
                        color: #000000;
                        span {
                            font-size: 14px;
                        }
                    }
                    .instock {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 1.4;
                        text-decoration-line: line-through;
                        color: #a6a6a6;
                    }
                    .input-quantity {
                        box-sizing: border-box;
                        display: flex;
                        margin-top: 10px;
                        align-items: center;
                        justify-content: space-evenly;
                        border: 1px solid #d9d9d9;
                        padding-left: 5px;
                        padding-right: 5px;
                        line-height: 2.5;
                        margin-bottom: 15px;
                        background: #ffffff;
                        border-radius: 3px;
                        .left-arrow {
                            display: inline-block;
                            width: 0;
                            height: 0;
                            border-style: solid;
                            border-width: 4px 10px 4px 0;
                            border-color: transparent #c4c4c4 transparent transparent;
                            cursor: pointer;
                        }
                        .right-arrow {
                            display: inline-block;
                            cursor: pointer;
                            width: 0;
                            height: 0;
                            border-style: solid;
                            border-width: 4px 0 4px 10px;
                            border-color: transparent transparent transparent #c4c4c4;
                        }
                        input {
                            width: 20%;
                            border: none;
                            text-align: center;
                            &:focus {
                                outline: none;
                            }
                        }
                    }
                }
                table {
                    box-sizing: border-box;
                    width: 100%;
                    border: 1px solid #e4e4e4;
                    background: #ffffff;
                    border-radius: 0 5px 0 0;
                    margin-bottom: 15px;
                    td {
                        padding: 13px;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 15px;
                        line-height: 1.8;
                        border-bottom: 1px solid #e4e4e4;
                        color: #707070;
                        border-right: 1px solid #e4e4e4;
                        &:nth-child(even) {
                            color: black;
                        }
                    }
                }
                .button-wrapper {
                    box-sizing: border-box;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 30px;
                    border: 1px solid #d9d9d9;
                    background: #ffffff;
                    border-radius: 3px;
                    .button {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 3;
                        text-align: center;
                        color: #727272;
                    }
                }
            }
            .images {
                .main-image {
                    img {
                        width: 100%;
                    }
                }
                .sub-images {
                    display: flex;
                    justify-content: space-between;
                    margin-top: 12px;
                    img {
                        width: 25%;
                    }
                }
            }
        }
        &--category {
            .item {
                margin-bottom: 25px;
                img {
                    width: 100%;
                }
                .body {
                    .instock {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 18px;
                        color: #a4a4a4;
                    }
                    .price {
                        font-size: 18px;
                    }
                }
            }
            .header {
                .title {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 32px;
                    line-height: 1.2;
                    color: #000000;
                }
                .subtitle {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 1.1;
                    color: #8a8a8a;
                    margin-bottom: 17px;
                    margin-top: 8px;
                }
                .button-wrapper {
                    display: flex;
                    align-items: center;
                    margin-bottom: 30px;
                    justify-content: space-between;
                    .button {
                        box-sizing: border-box;
                        width: 200.96px;
                        border: 1px solid #d9d9d9;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 2.7;
                        text-align: center;
                        background: #ffffff;
                        border-radius: 3px;
                        color: #727272;
                        cursor: pointer;
                    }
                    .app {
                        display: flex;
                        justify-content: center;
                        width: 39px;
                        height: 39px;
                        border: 1px solid #d9d9d9;
                        cursor: pointer;
                        img {
                            width: 20px;
                        }
                    }
                }
            }
        }
        &--slider {
            background: white;
            .arrows {
                box-sizing: border-box;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 96px;
                margin: 0 auto;
                border: 1px solid #e4e4e4;
                background: #ffffff;
                border-radius: 5px;
            }
            .left, .right {
                font-style: normal;
                font-weight: normal;
                font-size: 24px;
                line-height: 28px;
                text-align: center;
                color: #555555;
                width: 60px;
            }
            .left {
                border-right: 1px solid #dcdcdc;
            }
            img {
                width: 100%;
                margin-bottom: 18px;
            }
        }
        &--icons {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 36px 0 53px 0;
            background: #ffffff;
            .item {
                display: flex;
                .image {
                    margin-right: 18px;
                }
                .text {
                    padding-right: 20px;
                    .title {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 18px;
                        color: #000000;
                    }
                    .subtitle {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 12px;
                        line-height: 11px;
                        color: #9f9f9f;
                    }
                }
            }
        }
        &--block {
            background-color: white;
            padding-bottom: 31px;
            .header {
                font-style: normal;
                font-weight: normal;
                font-size: 32px;
                line-height: 27px;
                color: #000000;
                margin-bottom: 12px;
            }
            .body {
                .item {
                    .image {
                        margin-bottom: 12px;
                        img {
                            width: 100%;
                        }
                    }
                    .body {
                        .instock {
                            font-style: normal;
                            font-weight: normal;
                            font-size: 14px;
                            line-height: 18px;
                            color: #a4a4a4;
                        }
                        .price {
                            font-size: 18px;
                        }
                    }
                }
            }
            .footer {
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 30px 0 0 0;
                .button {
                    box-sizing: border-box;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 284px;
                    height: 30px;
                    border: 1px solid #d9d9d9;
                    background: #ffffff;
                    border-radius: 3px;
                }
            }
        }
        &--groups {
            padding-bottom: 33px;
            .header {
                .title {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 32px;
                    line-height: 30px;
                    text-align: center;
                    color: #000000;
                    margin-bottom: 1rem;
                }
                .subtitle {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 30px;
                    margin-bottom: 1rem;
                    text-align: center;
                    color: #949494;
                }
            }
            .body {
                .item {
                    .image {
                        margin-bottom: 12px;
                        img {
                            width: 100%;
                        }
                    }
                    .body {
                        .button-wrapper {
                            .button {
                                box-sizing: border-box;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                height: 38px;
                                font-style: normal;
                                font-weight: normal;
                                font-size: 14px;
                                line-height: 30px;
                                text-align: center;
                                color: #727272;
                                border: 1px solid #d9d9d9;
                                background: #ffffff;
                                border-radius: 3px;
                                margin-top: 7px;
                            }
                        }
                    }
                }
            }
        }
        &--footer {
            padding-bottom: 50px;
            h1 {
                margin-bottom: 15px;
            }
            h2 {
                margin-bottom: 15px;
            }
            p {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 18px;
                color: #888888;
            }
        }
    }
}
.section__footer {
    padding-top: 30px;
    .header {
        text-align: center;
        .image {
            display: flex;
            justify-content: center;
            margin-bottom: 10px;
            img {
                width: 100px;
            }
        }
        .title {
            font-style: normal;
            font-weight: bold;
            font-size: 32px;
            line-height: 18px;
            margin-bottom: 10px;
            color: #000000;
        }
        .subtitle {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 14px;
            text-align: center;
            color: #565656;
            margin-bottom: 10px;
        }
        .phone {
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 18px;
            color: #7d7d7d;
            margin-bottom: 10px;
        }
        .address {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 18px;
            color: #7d7d7d;
            margin-bottom: 10px;
        }
    }
    .navigation {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 246%;
        color: #7d7d7d;
        .title {
            font-weight: bold;
        }
        .buy {
            .title {
                font-weight: normal;
            }
        }
        .button {
            box-sizing: border-box;
            display: flex;
            align-items: center;
            margin-top: 11px;
            justify-content: center;
            width: 121px;
            height: 30px;
            border: 1px solid #d9d9d9;
            background: #ffffff;
            border-radius: 3px;
        }
        .socials {
            .title {
                font-weight: normal;
            }
            img {
                width: 30px;
            }
        }
    }
    &--wrapper {
        background: #ffffff;
        padding-top: 30px;
        padding-left: 25px;
        padding-right: 25px;
        padding-bottom: 30px;
    }
    &--basement {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        margin-top: 23px;
        line-height: 150%;
        color: #c7c7c7;
    }
}